jQuery(document).ready(function(){  
  jQuery('.search-complete input').autocomplete({
    source: '../wp-content/themes/Foundation-Sage/templates/mal-connect.php',
    minLength: 2,
    select: function(e, ui) {
      jQuery('.search-title input').val(ui.item.label);
      jQuery('.search-output input').val(ui.item.score);
    }
  });
});